import { Routes, Route } from "react-router-dom";

import Partido1 from "./comp/Partido1";
import Partido2 from "./comp/Partido2";
import Partido3 from "./comp/Partido3";
import Partido4 from "./comp/Partido4";
import Partido5 from "./comp/Partido5";
import Partido6 from "./comp/Partido6";
import Partido7 from "./comp/Partido7";
import Partido8 from "./comp/Partido8";
import Partido9 from "./comp/Partido9";
import Partido10 from "./comp/Partido10";
import Partido11 from "./comp/Partido11";
import Partido12 from "./comp/Partido12";
import Tests from "./comp/test";
function Apps() {
  return (
    <>
      <Routes>
        <Route path="/partido1" element={<Partido1 />} />
        <Route path="/partido2" element={<Partido2 />} />
        <Route path="/partido3" element={<Partido3 />} />
        <Route path="/partido4" element={<Partido4 />} />
        <Route path="/partido5" element={<Partido5 />} />
        <Route path="/partido6" element={<Partido6 />} />
        <Route path="/partido7" element={<Partido7 />} />
        <Route path="/partido8" element={<Partido8 />} />
        <Route path="/partido9" element={<Partido9 />} />
        <Route path="/partido10" element={<Partido10 />} />
        <Route path="/partido11" element={<Partido11 />} />
        <Route path="/partido12" element={<Partido12 />} />

        <Route path="/test" element={<Tests />} />
      </Routes>
    </>
  );
}

export default Apps;
